.todo {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  margin: 15px 0;
  font-size: 1.5rem;
  border-radius: 5px;
  border: 2px solid #555;
  color: #112d49;
  background-color: #fbfef9;
  cursor: pointer;
}

.todoText {
  width: 100%;
  text-align: left;
}

.todoIcon {
  font-size: 1.8rem;
  margin-right: 10px;
  color: teal;
}

.completedTodo {
  background-color: unset;
  border-color: gray;
  color: gray;
}

.todo.completedTodo .deleteIcon,
.todo.completedTodo .todoIcon {
  color: gray;
}
.todo.completedTodo .checkIcon {
  color: green;
}

.deleteIcon,
.checkIcon {
  cursor: pointer;
  font-size: 1.8rem;

  color: gray;
  padding: 0 7px;
  font-size: 1.8rem;
  transition: all 0.3s ease;
}

.deleteIcon:hover,
.checkIcon:hover {
  transform: scale(130%);
  cursor: pointer;
}
.deleteIcon:hover {
  color: orangered;
}
.checkIcon:hover {
  color: green;
}
